:root{
  --primary: #f84848;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.product-card__body{
  background-color: #efefef;
}

.product-card__container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  overflow: hidden;
}

.product-card__card{
  border-radius: 2px;
  overflow: hidden;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 860px;
  z-index: 1;
}

.product-card__photo-container{
  position: absolute;
  margin-left: 30px;
  overflow: hidden;
  position: relative;
  width:50%;
  height: 475px;
}

.product-card__gradients{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.product-card__gradient{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -2;
}

.product-card__first {
  z-index: 0;
  animation: 1s width ease;
}

.product-card__second {
  z-index: -1;
}

@keyframes width{
  from{
    width: 0%;
  }

  to{
    width: 100%;
  }
}

.product-card__gradient[color="blue"]{
  background-image: linear-gradient(45deg, #0136af, #22abfa);
}

.product-card__gradient[color="red"]{
  background-image: linear-gradient(45deg, #d62926, #ee625f);
}


.product-card__gradient[color="green"]{
  background-image: linear-gradient(45deg, #11998e, #1ce669);
}


.product-card__gradient[color="orange"]{
  background-image: linear-gradient(45deg, #fc4a14, #f7b733);
}


.product-card__gradient[color="black"]{
  background-image: linear-gradient(45deg, #111, #666);
}

.product-card__logo{
  z-index: 1;
  position: absolute;
  width: 100px;
  left: 20px;
  top: 20px;
}

.product-card__share{
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: #fff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  font-size: 1.3rem;
  text-decoration: none;
  color: #3a3a3a;
  transition: 0.5s;
}

.product-card__share:hover{
  transform: scale(1.1)
}

.product-card__share i{
  line-height: 50px;
}

.product-card__nike{
  position: absolute;
  top: 85px;
  left: 15px;
  font-size: 11rem;
  text-transform: uppercase;
  line-height: 0.9;
  color: #fff;
  opacity: 0.1;
}

.product-card__photo{
  width: 100%;
  transition: 0.5s;
}

.product-card__shoe.show{
  opacity: 1;
}

.product-card__info{
  width: 50%;
  background-color: #fff;
  z-index: 1;
  padding: 35px 40px;
}

.product-card__name{
  padding: 0 0 10px 0;
}

.product-card__name div{
  display: flex;
  align-items: center;
}

.product-card__name div .big{
  margin-right: 10px;
  font-size: 2rem;
  color: #333;
  line-height: 1;
}

.product-card__name div .new{
  background-color: var(--primary);
  text-transform: uppercase;
  color: #fff;
  padding: 3px 10px;
  border-radius: 5px;
}

.product-card__name .small{
  font-weight: 500;
  color: #525252;
  margin-top: 3px;
  text-transform: capitalize;
}

.product-card__showName, .product-card__description, .product-card__color-container, .product-card__size-container{
  border-bottom: 1px solid #dadada;
}

.product-card__description{
  padding: 10px 0;
}

.product-card__title{
  color: #3a3a3a;
  font-weight: 600;
  font-size: 1.2rem;
  text-transform: uppercase;
}

.product-card__text{
  color: #555;
  font-size: 17px;
  line-height: 1.5;
}

.product-card__color-container{
  padding: 10px 0;
}

.product-card__colors{
  padding: 8px 0;
  display: flex;
  align-items: center;
}

.product-card__color{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin: 0 10px;
  border: 5px solid;
  cursor: pointer;
  transition: .5s;
}

.product-card__color[color="blue"]{
  background-color: #2175f5;
  border-color: #2175f5;
}

.product-card__color[color="red"]{
  background-color: #f84848;
  border-color: #f84848;
}


.product-card__color[color="green"]{
  background-color: #29b864;
  border-color: #29b864;
}


.product-card__color[color="orange"]{
  background-color: #ff5521;
  border-color: #ff5521;
}


.product-card__color[color="black"]{
  background-color: #444;
  border-color: #444;
}

.product-card__color.active{
  border-color: #fff;
  box-shadow: 0 0 10px .5px rgba(0, 0 ,0, 0.2);
  transform: scale(1.1);
}

.product-card__size-container{
  padding: 10px 0;
  margin-bottom: 10px;
}

.product-card__sizes{
  padding: 8px 0;
  display: flex;
  align-items: center;
}

.product-card__size{
  width: 40px;
  height: 40px;
  border-radius: 6px;
  color: rgb(17, 17, 17);
  box-shadow: rgb(229, 229, 229) 0px 0px 0px 1px;
  background: #fff;
  margin: 0 10px;
  text-align: center;
  line-height: 40px;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: .3s;
}

.product-card__size.active{
  background-color: var(--primary);
  color: #fff;
}

.product-card__buy-price{
  flex-direction: column;
  padding-top: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-card__price{
  margin-top: 10px;
  color: #333;
  display: flex;
  align-items: flex-start;
}

.product-card__price h1{
  font-size: 2.1rem;
  font-weight: 600;
  line-height: 1;
}

.product-card__price i{
  font-size: 1.4rem;
  margin-right: 1px;
}

.product-card__buy {
  width: 100%;
  text-align: center;
  padding: .7rem 2rem;
  background: #111;
  color: #fff;
  letter-spacing: 1px;
  font-weight: 500;
  font-size: 1.1rem;
  transition: .5s;
  text-decoration: none;
  border-radius: 30px;
  margin-bottom: 10px;
  border: 1px solid #111;
}

.product-card__buy:hover {
  color: #111111;
  background: #fff;
  border: 1px solid #ececec;
}

.product-card__buy i {
  margin-right: 8px;
}

/* Responsive */
@media(max-width: 1070px){
  .product-card__photo{
    width: 100%;
  }
}

@media(max-width: 1000px){
  .product-card__card{
    flex-direction: column;
    width: 100%;
    box-shadow: 0 0 35px 1px rgba(0, 0, 0, 0.2);
  }

  .product-card__card > div {
    width: 100%;
    box-shadow: none;
  }

  .product-card__photo {
    width: 100%;
  }

  .product-card__nike{
    top: 20%;
    left: 5%;
  }

  .product-card__photo-container{
    margin: 0;
  }
}

@media (max-width: 600px){
  .product-card__share{
    width: 40px;
    height: 40px;
  }

  .product-card__share i {
    font-size: 1rem;
    line-height: 40px;
  }

  .product-card__logo{
    width: 70px;
  }
}

@media(max-width: 500px){
  .product-card__nike{
    font-size: 7rem;
  }

  .product-card__name div .big{
    font-size: 1.3rem;
  }

  .small{
    font-size: 1rem;
  }

  .product-card__new{
    padding: 2px 6px;
    font-size: .9rem;
  }

  .product-card__title{
    font-size: .9rem;
  }

  .product-card__text{
    font-size: .8rem;
  }

  .product-card__buy{
    padding: .5rem .8rem;
    font-size: 1rem;
  }

  .product-card__price h1{
    font-size: 1.5rem;
  }

  .product-card__price i{
    font-size: .9rem;
  }

  .product-card__size{
    width: 30px;
    height: 30px;
    margin: 0 8px;
    font-size: .9rem;
    line-height: 30px;
  }

  .product-card__color{
    margin: 0 20px;
    width: 20px;
    height: 20px;
    border-width: 4px;
  }

  .product-card__share{
    width: 35px;
    height: 35px;
    top: 10px;
    right: 10px;
  }

  .product-card__share i{
    font-size: .8rem;
    line-height: 35px;
  }

  .product-card__logo{
    width: 60px;
    top: 10px;
    left: 10px;
  }

  .product-card__info{
    padding: 20px;
  }

  .product-card__photo-container{
    margin: 0;
  }
}

@media (max-width: 500px){
  .product-card__buy i {
    display: none;
  }

  .product-card__container{
    padding: 0;
  }
}